
import { Component, Prop, Vue } from 'vue-property-decorator';

@Component<VueYandexShare>({})
export default class VueYandexShare extends Vue {
  /**
   *        .  
   *    Facebook
   */
  @Prop({ type: String })
  private accessToken!: string;

  /**
   *  ,    .   ,
   *        
   */
  @Prop({
    type: Boolean,
    default: false,
  })
  private bare!: boolean;

  /**
   *  ,       
   */
  @Prop({
    type: Boolean,
    default: false,
  })
  private counter!: boolean;

  /**
   *    .    
   * ,    limit
   */
  @Prop({
    type: String,
    default: 'last',
    validator(value) {
      return ['first', 'last', 'hidden'].includes(value);
    },
  })
  private copy!: 'first' | 'last' | 'hidden';

  /**
   * ,   
   */
  @Prop({
    type: String,
  })
  private description!: string;

  /**
   *   
   */
  @Prop({
    type: String,
    default: 'horizontal',
    validator(value) {
      return ['horizontal', 'vertical'].includes(value);
    },
  })
  private direction!: 'horizontal' | 'vertical';

  /**
   * .      
   */
  @Prop({
    type: String,
  })
  private hashtags!: string;

  /**
   * ,   
   */
  @Prop({
    type: String,
  })
  private image!: string;

  /**
   *  .       
   * 
   */
  @Prop({
    type: String,
    default: 'ru',
    validator(value) {
      return ['az', 'be', 'en', 'hy', 'ka', 'kk', 'ro', 'ru', 'tr', 'tt', 'uk'].includes(value);
    },
  })
  private lang!: 'az' | 'be' | 'en' | 'hy' | 'ka' | 'kk' | 'ro' | 'ru' | 'tr' | 'tt' | 'uk';

  /**
   *  ,    .   
   *     ,        
   * .         pop-up 
   *  
   */
  @Prop({
    type: Number,
  })
  private limit!: number;

  /**
   *   pop-up
   */
  @Prop({
    type: String,
    default: 'bottom',
    validator(value) {
      return ['bottom', 'top'].includes(value);
    },
  })
  private popupDirection!: 'bottom' | 'top';

  /**
   *  pop-up   .  outer 
   *    ,  -    
   * pop-up    
   */
  @Prop({
    type: String,
    default: 'inner',
    validator(value) {
      return ['inner', 'outer'].includes(value);
    },
  })
  private popupPosition!: 'inner' | 'outer';

  /**
   *   
   */
  @Prop({
    type: String,
    default: 'm',
    validator(value) {
      return ['m', 's'].includes(value);
    },
  })
  private size!: 'm' | 's';

  /**
   * ,   
   */
  @Prop({
    type: String,
  })
  private title!: string;

  /**
   * ,   
   */
  @Prop({
    type: String,
  })
  private url!: string;

  /**
   *   
   */
  @Prop({
    type: String,
    default() {
      return [
        'blogger',
        'delicious',
        'digg',
        'evernote',
        'facebook',
        'gplus',
        'linkedin',
        'lj',
        'moimir',
        'odnoklassniki',
        'pinterest',
        'pocket',
        'qzone',
        'reddit',
        'renren',
        'sinaWeibo',
        'skype',
        'surfingbird',
        'telegram',
        'tencentWeibo',
        'tumblr',
        'twitter',
        'viber',
        'vkontakte',
        'whatsapp',
      ].join();
    },
  })
  private services!: string;

  /**
   *      
   */
  isScriptInserted: boolean = false;

  /**
   *   YandexShare
   */
  async loadAPIScript() {
    /**
     *  
     */
    const src: string = '//yastatic.net/share2/share.js';

    if (!document.querySelectorAll(`[src*='${src}']`).length) {
      const script = document.createElement('script');
      script.setAttribute('src', '//yastatic.net/share2/share.js');
      script.setAttribute('async', '');
      script.setAttribute('defer', '');

      document.body.appendChild(script);

      script.onload = () => {
        this.$emit('load');
      };
      script.onerror = error => {
        this.$emit('error', error);
      };
    } else {
      this.$emit('load');
    }
  }

  created() {
    this.loadAPIScript();
  }
}
